<template>
  <div class="root">
    <div class="container">
      <div class="kv">
        <div class="kv_image"></div>
      </div>
      <div class="router_container">
        <div class="block_container">
          <router-link class="router_block link" :class="isActive('Flow')"       to="Flow"      >关于流程</router-link>
          <router-link class="router_block link" :class="isActive('Deliver')"    to="Deliver"   >关于投递</router-link>
          <router-link class="router_block link" :class="isActive('Written')"    to="Written"   >关于笔试</router-link>
          <router-link class="router_block link" :class="isActive('InterView')"  to="InterView" >关于面试</router-link>
          <router-link class="router_block link" :class="isActive('InternShip')" to="InternShip">实习报到</router-link>
        </div>
      </div>

      <router-view></router-view>

      <div class="button_container">
        <button class="deliver_button"><a href="https://www.nowcoder.com/careers/bolegame/68408?ncsr=DmoDPg"  target="_blank" style="text-decoration: none;display: block;color: inherit">投递简历</a></button>
      </div>
    </div>

  </div>
</template>

<script>
import TopBar from "../components/TopBar";
import Footer from "../components/Footer";
export default {
  name: "FAQ",
  components: {Footer, TopBar},
  methods:{
    jump2Deliver(){
      alert('跳转到投递界面')
      console.log(this.$route.name)
    },
    isActive(name){
      if(this.$route.name === name){
        return 'active'
      }
      return 'normal'
    }
  }
}
</script>

<style scoped>
.root{
  /*border: red solid 1px;*/
}

@media screen and (min-width: 800px){
  .kv{
    height: calc(80vh);
    display: block;
    /*border: red solid 1px;*/
  }

  .kv_image{
    display: block;
    height: calc(80vh);
    /*border: blue solid 1px;*/
    background-image: url("https://h5.store.bolegames.com/bole_campus/assets/FAQ/kv_faq.jpg");
    background-position: center;
    background-size: cover;
  }


  /***************************   Router start   *************************/
  .router_container{
    /*border: #3B4BEC solid 1px;*/
    margin-top: 2vh;
    margin-bottom: 2vh;
    text-align: center;
  }
  .block_container{
    /*border: yellow solid 1px;*/
    width: 60%;
    display: inline-block;
  }
  .router_block{
    /*border: black solid 1px;*/
    border: #f6f1f1 solid 0.1vh;
    margin: 0 auto;
    float: left;
    display: block;
    width: 19%;
    padding-top: 0.8vh;
    padding-bottom: 0.8vh;

    font-size: 2vh;
  }
  .link{
    margin: 0 auto;
    text-decoration: none;
    color:inherit
  }

  .active{
    background-color: #1E90FF;

    color: #FFFFFF!important;
  }
  .normal{
    background-color: #FFFFFF;
    color: grey;
  }

  .button_container{
    /*border: red solid 1px;*/
    margin: auto;
    padding-top: 2vh;
    width: 56%;
  }

  .deliver_button{
    background-color: #1E90FF;
    padding: 1vh 3vh;
    border-radius: 1.5vh;
    border: #FFFFFF;
    font-size: 2vh;
    color: #FFFFFF;
  }
}
@media screen and (max-width: 800px){
  .kv{
    height: calc(40vh);
    display: block;
  }
  .kv_image{
    display: block;
    height: calc(40vh);
    /*border: blue solid 1px;*/
    background-image: url("https://h5.store.bolegames.com/bole_campus/assets/FAQ/kv_faq.jpg");
    background-position: center;
    background-size: cover;
  }


  /***************************   Router start   *************************/
  .router_container{
    /*border: #3B4BEC solid 1px;*/
    margin-top: 30px;
    margin-bottom: 30px;
    text-align: center;
  }
  .block_container{
    /*border: yellow solid 1px;*/
    width: 100%;
    display: inline-block;
  }
  .router_block{
    /*border: black solid 1px;*/
    border: #e2d2d2 solid 1px;
    margin: 0 auto;
    /*float: left;*/
    display: block;
    width: 100%;
    padding-top: 8px;
    padding-bottom: 8px;
  }
  .link{
    margin: 0 auto;
    text-decoration: none;
    color:inherit
  }

  .active{
    background-color: #1E90FF;

    color: #FFFFFF!important;
  }
  .normal{
    background-color: #FFFFFF;
    color: grey;
  }

  .button_container{
    /*border: red solid 1px;*/
    margin: auto;
    padding-top: 40px;
    width: 56%;
  }

  .deliver_button{
    background-color: #1E90FF;
    padding: 10px 60px;
    border-radius: 5px;
    border: #FFFFFF;
    font-size: medium;
    color: #FFFFFF;
  }
}

.container{
  /*border: #97A0F5 solid 1px;*/
  position: relative;
  margin-bottom: 20px;
}

</style>



