<template>
  <div class="root" id="topbarid">
    <div class="nav-container" style="overflow: hidden;">
      <div class="logo">
        <router-link to="/"><img src="https://h5.store.bolegames.com/bole_campus/assets/TopBar/logo.png" alt="logo" width="100%" height="100%"></router-link>
      </div>
      <el-menu class="el-menu-demo" mode="horizontal" router="true" active-text-color="#FFF" background-color="#FFF">

        <el-menu-item class="el-menu-item-group" :class="isActive(0)" index="/"   ><div class="item_text">校招首页</div></el-menu-item>
        <el-menu-item class="el-menu-item-group" :class="isActive(1)" index="/faq"><div class="item_text">校招FAQ</div></el-menu-item>
      </el-menu>
    </div>
  </div>
</template>

<script>
export default {
  name: "TopBar",
  methods:{
    isActive(id){
      if(this.$route.name === 'Home'){
        return id === 0 ? 'active' : 'normal'
      }
      return id === 0 ? 'normal' : 'active'
    }
  }
}
</script>

<style scoped>
@media screen and (min-width: 801px){
  .root{
    /*border: blue solid 1px;*/
    /*height: 90px;*/
    position: relative;
    /*height: 8vh;*/
    /*overflow:hidden;*/
  }

  .nav-container{
    width: 80%;
    /*border: red solid 2px;*/
    margin: 0 auto;
    /*height: 25vh;*/
    position: relative;
  }
  .logo{
    float: left;
    /*height: 60px;*/
    /*border: red 2px solid;*/
    /*margin-top: 5px;*/
    height: 8vh;
    line-height: 8vh;
    display:inline-block;
    /*vertical-align: center;*/
  }

  .el-menu-demo{
    float: right;
    /*border: green 2px solid;*/
    height: 8vh;
    /*line-height: 8vh;*/
    width: 15vw;
    margin: 0 auto;
    text-align: center;
    padding: 0;
  }

  .el-menu-item-group{
    float: left;
    /*border: red 1px solid;*/
    padding: 0;
    width: 50%;
    height: 100%;
    display: inline-block;
    line-height: 8vh;
    margin: 0 auto;
  }

  .item_text{
    float: none;
    font-family: "微软雅黑", "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    font-size: 2.5vh;
    margin: 0 auto;
    display: block;
  }
  .el-menu-item-group:hover{
    background-color: #1E90FF!important;
    color:            #FFF   !important;
    cursor:           default;
  }
  .active{
    background-color: #3b4cec !important;
    color: #FFFFFF!important;
  }
  .normal{
    background-color: #FFFFFF!important;
    color: #000000 !important;
  }
}
@media screen and (max-width: 800px) {
  .root{
    /*border: blue solid 1px;*/
    /*height: 10vh;*/
    overflow: hidden;
  }
  .nav-container{
    width: 100%;
    /*border: red solid 2px;*/
    display: block;
  }
  .logo{
    /*float: left;*/
    /*height: 60px;*/
    width: 40vw;
    /*border: red 2px solid;*/
    display:none;
    margin: 0 auto;
  }


  .el-menu-demo{
    margin: 0;
    padding: 0;
    overflow: hidden;
    /*float: right;*/
    /*border: green 2px solid;*/
  }

  .el-menu-item-group {
    float: left;
    /*border: red 1px solid;*/
    padding: 20px;
    display: block;
    margin-bottom: 0;

    font-family: "微软雅黑", "Helvetica Neue", elvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    font-size: 22px;
  }
  /*}*/
  /*.el-menu-item-group:hover{*/
  /*  background-color: #1E90FF!important;*/
  /*  color:            #FFF   !important;*/
  /*  cursor:           default;*/
  /*}*/

  .active{
    background-color: #3b4cec !important;
    color: #FFFFFF!important;
  }
  .normal{
    background-color: #FFFFFF!important;
    color: #000000 !important;
  }
}

</style>



